import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: 'app-forgett-password',
  templateUrl: './forgett-password.page.html',
  styleUrls: ['./forgett-password.page.scss'],
})
export class ForgettPasswordPage implements OnInit {
  phoneNumber = new FormControl('');
  routerData: any;
  isLoading: boolean = false;

  constructor(private router: Router,
    public toastCtrl: ToastController,
    private api: ApiService) { }

  ngOnInit() {
    if (this.routerData = this.router.getCurrentNavigation().extras.state) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      this.phoneNumber.setValue(this.routerData.data);
    }
  }

  back() {
    this.router.navigateByUrl('/login');
  }

  async openToast(message) {
    const toast = await this.toastCtrl.create({
      message: `${message}`,
      duration: 2500,
      position: 'middle',
    });
    toast.present();
  }

  getOtp() {
    const pattern = /^([\+0-9]{3}[0-9]{10}|[0-9]{10})$/;
    if (pattern.test(this.phoneNumber.value)) {
      this.isLoading = true;
      this.api.custpomerDetails(this.phoneNumber.value).subscribe((resp: any) => {
        this.isLoading = false;
        if (resp.custStatus === 'APPROVED') {
          resp.custAccount = resp.custAccount.filter(
            (card: any) => card.status === 'APPROVED'
          );
          localStorage.setItem('customerPhonenum', this.phoneNumber.value);
          this.router.navigateByUrl('/otp', { state: { resetPass: true } });
        } else {
          this.openToast(
            'Your account status is not approved'
          );
          this.router.navigate(['login'], { replaceUrl: true });
          this.isLoading = false;
        }
      });
    } else {
      this.openToast('Enter valid phone number');

    }
  }
}
